//    

function Jumbotron(props) {//props    (=)
    
    return (
        <>
            <div className="row">
                <div className="col">
                    <div className="p-4 bg-dark text-light rounded">
                        <h1>{props.title}</h1>
                        <p>{props.content}</p>
                    </div>
                </div>
            </div>
        </>
    );
}

export default Jumbotron;