//Recoil      
// -  Spring Boot  HttpSession  

import {atom, selector} from "recoil";

//atom recoil    
const countState = atom({
    key : 'countState',//(ID)
    default : 0//
});

//   
const loginIdState = atom({
    key : 'loginIdState',
    default : ''
});
const loginLevelState = atom({
    key : 'loginLevelState',
    default : ''
});

//atom       (selector)
const isLoginState = selector({
    key : "isLoginState", // (       )
    get : (state)=>{//state      
        //  state  loginIdState   !
        const loginId = state.get(loginIdState);
        //  state  loginIdState   !
        const loginLLevel = state.get(loginLevelState);

        return loginId && loginId.length > 0 
                && loginLLevel && loginLLevel.length > 0 

    }
})

//default export    
//export default countState;

//naming export     .
export {countState, loginIdState, loginLevelState, isLoginState};